/* eslint-disable implicit-arrow-linebreak */
/* eslint-disable no-confusing-arrow */
/* eslint-disable multiline-ternary */
import {
  Fragment,
  useState,
  useEffect,
  useContext,
  useMemo,
  useCallback,
  forwardRef,
  lazy,
  Suspense
} from "react"
import classnames from "classnames"
import { Link, useNavigate, useLocation } from "react-router-dom"
import { useTranslation } from "react-i18next"
import { AbilityContext } from "@src/utility/context/Can"
import ModalAddEdit from "./modal"
import { neutralizeBack, revivalBack } from "@src/utility/Utils"
import GlobalActionsTable from "../../../shared/GlobaActionsTable"
import {
  storeName,
  pageTitle,
  serverSideGrid,
  detailFromGrid,
  deleteSelection,
  getApiAfterPut,
  getApiAfterPost,
  getApiAfterDelete,
  getSummarysData,
  detailPage,
  getAlwaysGrid,
  getDataList,
  getDataById,
  setDataById,
  resetSelectedData,
  deleteData,
  deleteSelectedRowData,
  dynamicColumnsFilter
} from "./store"
import { getInvoice } from "../../invoices/store"
import { useDispatch, useSelector } from "react-redux"
import ReactPaginate from "react-paginate"
import DataTable from "react-data-table-component"
import debounce from "lodash.debounce"
import {
  ChevronDown,
  Share,
  Printer,
  FileText,
  File,
  Grid,
  Copy,
  Slack,
  User,
  Settings,
  Database,
  Edit2,
  MoreVertical,
  Trash2,
  Archive,
  Eye,
  CheckCircle,
  CheckSquare,
  Check,
  Square
} from "react-feather"

import { getUserData } from "@utils"
import { NoDataDisplay, Loader } from "@src/shared/CommonComponents"
import UILoader from "@components/ui-loader"
import Spinner from "@components/spinner/Loading-spinner"
import { ThemeColors } from "@src/utility/context/ThemeColors"
import toast from "react-hot-toast"
import {
  Row,
  Col,
  Card,
  Input,
  Label,
  Button,
  CardBody,
  CardTitle,
  CardHeader,
  DropdownMenu,
  DropdownItem,
  DropdownToggle,
  UncontrolledDropdown,
  Badge,
  UncontrolledTooltip,
  CardText
} from "reactstrap"

import "@styles/react/libs/react-select/_react-select.scss"
import "@styles/react/libs/tables/react-dataTable-component.scss"
import Swal from "sweetalert2"
import withReactContent from "sweetalert2-react-content"
import { Skeleton } from "@mui/material"
import Moment from "moment"

const MySwal = withReactContent(Swal)
const BootstrapCheckbox = forwardRef((props, ref) => (
  <div className="form-check">
    <Input type="checkbox" ref={ref} {...props} />
  </div>
))

// ** Table Header
const CustomHeader = ({
  store,
  handleAddEdit,
  handlePerPage,
  rowsPerPage,
  handleFilter,
  searchTerm,
  selectedRow,
  handleDeleteSelected,
  handleFiltersChange,
  filtersCustomAdd
}) => {
  const { t } = useTranslation()
  const ability = useContext(AbilityContext)
  const dispatch = useDispatch()
  const allStore = useSelector((state) => state)
  const [dynamicFilterComponents, setDynamicFilterComponents] = useState({})
  const [dynamicFilterDatas, setDynamicFilterDatas] = useState({})

  const handleChangeInputs = (value, fieldName, isMulti = false) => {
    fieldName = `filter_${fieldName}`
    if (isMulti) {
      if (dynamicFilterDatas[fieldName]) {
        const index = dynamicFilterDatas[fieldName].indexOf(value)
        if (index !== -1) {
          dynamicFilterDatas[fieldName].splice(index, 1)
        } else {
          dynamicFilterDatas[fieldName].push(value)
        }
      } else {
        dynamicFilterDatas[fieldName] = [value]
      }
    } else {
      dynamicFilterDatas[fieldName] = value
    }
    return handleFiltersChange(dynamicFilterDatas[fieldName], fieldName)
  }

  useEffect(() => {
    // DYNAMIC IMPORT COMPONENTS
    let componentsImport = {}
    for (let i = 0; i < dynamicColumnsFilter.length; i++) {
      const fieldName = dynamicColumnsFilter[i]["fieldName"]
      const path_name = dynamicColumnsFilter[i]["fieldName"].replaceAll(
        "id_",
        ""
      )

      dynamicFilterDatas[`filter_${fieldName}`] = null
      const MyComponent = lazy(() => import(`../${path_name}`))
      componentsImport[`${path_name}_comp`] = (
        <MyComponent
          lookupMode={true}
          lookupMode_fieldName={fieldName}
          lookupMode_dropdown={true}
          lookupMode_isMulti={dynamicColumnsFilter[i].isMultiSelection ?? false}
          lookupMode_onChange={handleChangeInputs}
          lookupMode_defaultValue={
            dynamicColumnsFilter[i]?.isM2MDefValFromGrid
              ? allStore[storeName].data.map((x) => x[fieldName])
              : allStore[storeName]?.selectedData
              ? allStore[storeName].selectedData[fieldName]
              : ""
          }
          lookupMode_isM2MDefValFromGrid={
            dynamicColumnsFilter[i]?.isM2MDefValFromGrid ?? false
          }
        />
      )
    }

    setDynamicFilterComponents(componentsImport)
  }, [dispatch, dynamicFilterDatas])

  return (
    <Row className="text-nowrap w-100 my-75 g-0 permission-header">
      <Col xs={12} lg={4} className="d-flex align-items-center">
        <div className="d-flex align-items-center justify-content-center justify-content-lg-start">
          <label htmlFor="rows-per-page">{t("Show")}</label>
          <Input
            className="mx-50"
            type="select"
            id="rows-per-page"
            value={rowsPerPage}
            onChange={handlePerPage}
            style={{ width: "5rem" }}
          >
            <option value="10">10</option>
            <option value="25">25</option>
            <option value="50">50</option>
          </Input>
          <label htmlFor="rows-per-page">{t("Entries")}</label>
        </div>
      </Col>
      <Col xs={12} lg={8}>
        <div className="d-flex align-items-center justify-content-lg-end justify-content-start flex-md-nowrap flex-wrap mt-lg-0 mt-1">
          <div className="d-flex align-items-center me-1">
            <label className="mb-0" htmlFor="search-invoice">
              {t("Search")}:
            </label>
            <Input
              id="search-invoice"
              className="ms-50 w-100"
              type="text"
              value={searchTerm}
              onChange={(e) => handleFilter(e.target.value)}
            />
          </div>
          {/* <div className="mt-50 width-200 me-1 mt-sm-0 mt-1">
            <Input
              type="select"
              name="select"
              // value={role}
              onChange={(e) => handleAssignedToChange(e.target.value)}
            >
              <option value="">Select Role</option>
              <option value="administrator">Administrator</option>
              <option value="manager">Manager</option>
              <option value="user">User</option>
              <option value="support">Support</option>
              <option value="restricted-user">Restricted User</option>
            </Input>
          </div> */}
          {dynamicColumnsFilter.map((item, index) => {
            const path_name = item.fieldName.replaceAll("id_", "")
            return (
              <div
                key={path_name}
                className="mt-50 width-300 me-1 mt-sm-0 mt-1"
              >
                <Suspense fallback={<div>Loading...</div>}>
                  {dynamicFilterComponents[`${path_name}_comp`]}
                </Suspense>
              </div>
            )
          })}

          {ability.can("post", sessionStorage.getItem("current_page_perm")) ? (
            <Button
              className="add-permission mt-sm-0 mt-1"
              color="primary"
              // onClick={toggleSidebar}
              onClick={handleAddEdit}
            >
              {t("Add New")} {t(pageTitle)}
            </Button>
          ) : null}
        </div>
      </Col>
    </Row>
  )
}

const List = ({ filtersCustomAdd }) => {
  const dispatch = useDispatch()
  const store = useSelector((state) => state[storeName])

  const { t } = useTranslation()
  const ability = useContext(AbilityContext)

  const { colors } = useContext(ThemeColors)
  const [sort_dir, setSortDir] = useState("desc")
  const [searchTerm, setSearchTerm] = useState("")
  const [currentPage, setCurrentPage] = useState(1)
  const [sort, setSort] = useState("id")
  const [rowsPerPage, setRowsPerPage] = useState(10)
  const [selectedRows, setSelectedRows] = useState([])

  const [filtersCustom, setFiltersCustom] = useState(filtersCustomAdd ?? {})

  const [toggleCleared, setToggleCleared] = useState(false)

  const [sidebarOpen, setSidebarOpen] = useState(false)
  const [loading, setLoading] = useState(false)
  const [loadingText, setLoadingText] = useState("Please Wait")
  const [showModal, setShowModal] = useState(false)
  const [formState, setFormState] = useState(null)
  const navigate = useNavigate()
  const location = useLocation()
  const colorsAssignee = {
    Viewer: "light-info",
    Editor: "light-success",
    Manager: "light-warning",
    Administrator: "light-primary",
    "restricted-user": "light-danger"
  }

  const handleAddEdit = (rowData) => {
    // console.log(rowData.id)
    if (!rowData.id) {
      setFormState("edit")
    } else {
      setFormState("add")
    }
    //
    if (!detailPage) {
      if (!detailFromGrid) {
        setLoadingText(
          `${t("Loading")} ${t("Detail")} ${t("Data")} ${t(pageTitle)}`
        )
        setLoading(true)
        dispatch(getDataById(rowData.id)).then((res) => {
          if (res.meta.requestStatus === "rejected") {
            setLoading(false)
            toast.error(res.payload.message, {
              style: {
                padding: "16px",
                color: colors.danger.main,
                border: `1px solid ${colors.danger.main}`
              },
              iconTheme: {
                primary: colors.danger.main
              }
            })
          } else {
            setLoading(false)
            setShowModal(true)
          }
        })
      } else {
        setLoadingText(
          `${t("Loading")} ${t("Detail")} ${t("Data")} ${t(pageTitle)}`
        )
        setLoading(true)
        dispatch(setDataById(rowData)).then(() => {
          setLoading(false)
          setShowModal(true)
        })
      }
    } else {
      if (rowData.id) {
        navigate(`${location.pathname}/edit/${rowData.id}`)
      } else {
        dispatch(resetSelectedData({})).then((r) => {
          navigate(`${location.pathname}/add`)
        })
      }
    }
  }

  const handleCloseModal = () => {
    setSidebarOpen(!sidebarOpen)
    setShowModal(!showModal)
    revivalBack()
  }

  const toggleSidebar = () => {
    // console.log("here")
    setFormState("add")
    setSidebarOpen(!sidebarOpen)
    setShowModal(!showModal)
    neutralizeBack(handleCloseModal)
  }

  const handleDelete = async (id) => {
    const result = await MySwal.fire({
      title: `${t("Are you sure?")}`,
      text: `${t("You won't be able to revert")} ${t(pageTitle)}!`,
      icon: "warning",
      showCancelButton: true,
      confirmButtonText: `${t("Yes, Delete")} ${t(pageTitle)}!`,
      cancelButtonText: `${t("Cancel")}`,
      customClass: {
        confirmButton: "btn btn-primary",
        cancelButton: "btn btn-outline-danger ms-1"
      },
      buttonsStyling: false
    })
    if (result.value) {
      setLoadingText(`${t("Deleting")} Data ${t(pageTitle)}`)
      setLoading(true)
      dispatch(deleteData(id)).then((res) => {
        // console.log(res)
        if (res.meta.requestStatus === "fulfilled") {
          setLoading(false)
          toast.success(`${t("Successfully deleted")} ${t(pageTitle)}`, {
            style: {
              padding: "16px",
              color: colors.success.main,
              border: `1px solid ${colors.success.main}`
            },
            iconTheme: {
              primary: colors.success.main
            }
          })
        } else if (res.meta.requestStatus === "rejected") {
          setLoading(false)
          toast.error(res.payload.message, {
            style: {
              padding: "16px",
              color: colors.danger.main,
              border: `1px solid ${colors.danger.main}`
            },
            iconTheme: {
              primary: colors.danger.main
            }
          })
        }
      })
    }
  }

  const handleDeleteSelected = async () => {
    const countSelectedCopy = selectedRows.length
    const result = await MySwal.fire({
      title: `${t("Are you sure?")}`,
      text: `${t("You will delete")} ${countSelectedCopy} ${t(
        "selected data. Deleted data cannot be recovered"
      )}!`,
      icon: "warning",
      showCancelButton: true,
      confirmButtonText: `${t("Yes, Delete")} ${countSelectedCopy} ${t(
        pageTitle
      )}!`,
      cancelButtonText: `${t("Cancel")}`,
      customClass: {
        confirmButton: "btn btn-primary",
        cancelButton: "btn btn-outline-danger ms-1"
      },
      buttonsStyling: false
    })
    if (result.value) {
      setLoadingText(
        `${t("Deleting")} ${countSelectedCopy} Data ${t(pageTitle)}`
      )
      setLoading(true)
      const values = selectedRows.map((a) => a.id)
      // console.log(values)
      dispatch(deleteSelectedRowData({ id: values })).then((res) => {
        // console.log(res)
        if (res.meta.requestStatus === "fulfilled") {
          setSelectedRows([])
          setToggleCleared(!toggleCleared)
          setLoading(false)
          toast.success(
            `${t("Successfully deleted")} ${countSelectedCopy} ${t(pageTitle)}`,
            {
              style: {
                padding: "16px",
                color: colors.success.main,
                border: `1px solid ${colors.success.main}`
              },
              iconTheme: {
                primary: colors.success.main
              }
            }
          )
        } else if (res.meta.requestStatus === "rejected") {
          setLoading(false)
          toast.error(res.payload.message, {
            style: {
              padding: "16px",
              color: colors.danger.main,
              border: `1px solid ${colors.danger.main}`
            },
            iconTheme: {
              primary: colors.danger.main
            }
          })
        }
      })
    }
  }

  /////PAGINATION SERVERSIDE
  const handlePagination = (page) => {
    dispatch(
      getDataList({
        sort_dir,
        sort,
        search: searchTerm,
        length: rowsPerPage,
        page: page.selected + 1,
        ...filtersCustom
      })
    )
    setCurrentPage(page.selected + 1)
  }

  const handlePerPage = (e) => {
    const value = parseInt(e.currentTarget.value)
    dispatch(
      getDataList({
        sort_dir,
        sort,
        search: searchTerm,
        length: value,
        page: currentPage,
        ...filtersCustom
      })
    )
    setRowsPerPage(value)
  }

  const handleDebounceFn = (inputValue) => {
    dispatch(
      getDataList({
        sort_dir,
        search: inputValue,
        sort,
        page: currentPage,
        length: rowsPerPage,
        ...filtersCustom
      })
    )
  }

  const debounceFn = useCallback(debounce(handleDebounceFn, 500), [])

  const handleFilter = (event) => {
    setSearchTerm(event)
    debounceFn(event)
  }

  const handleFiltersChange = (value, par_name) => {
    filtersCustom[par_name] = value
    dispatch(
      getDataList({
        sort_dir,
        sort,
        search: searchTerm,
        page: currentPage,
        length: rowsPerPage,
        ...filtersCustom
      })
    )
  }

  const handleSort = (column, sortDirection) => {
    setSortDir(sortDirection)
    setSort(column.sortField)
    dispatch(
      getDataList({
        sort_dir,
        sort,
        search: searchTerm,
        page: currentPage,
        length: rowsPerPage,
        ...filtersCustom
      })
    )
  }

  const handleRowSelected = useCallback((state) => {
    setSelectedRows(state.selectedRows)
  }, [])

  const CustomPagination = () => {
    const count = Number(Math.ceil(store.total / rowsPerPage))

    return (
      <ReactPaginate
        previousLabel={""}
        nextLabel={""}
        pageCount={count || 1}
        activeClassName="active"
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={(page) => handlePagination(page)}
        pageClassName={"page-item"}
        nextLinkClassName={"page-link"}
        nextClassName={"page-item next"}
        previousClassName={"page-item prev"}
        previousLinkClassName={"page-link"}
        pageLinkClassName={"page-link"}
        containerClassName={
          "pagination react-paginate justify-content-end my-2 pe-1"
        }
      />
    )
  }

  const conditionalRowStyles = [
    {
      when: (row) => Array.isArray(row),
      classNames: ["d-table"]
    }
  ]

  const columns = [
    ...store.dynamicColumns.map((column) => ({
      name: column.columnName,
      sortField: column.fieldName,
      center: false,
      wrap: true,
      grow: 0,
      sortable: true,
      width: "250px",
      selector: (row) => row[column.fieldName],
      cell: (row) => renderCell(row, column)
    })),
    {
      name: t("Title"),
      center: false,
      sortable: true,
      wrap: true,
      width: "350px",
      sortField: "title",
      selector: (row) => row.title,
      cell: (row) => {
        const title = row.title,
          description = row.description ? row.description : ""
        return (
          <div className="d-flex justify-content-left align-items-center">
            <div className="d-flex flex-column">
              <h6 className="user-name text-truncate mb-0">{title}</h6>
              <small className="text-truncate text-muted mb-0">
                {description}
              </small>
            </div>
          </div>
        )
      }
    },

    // {
    //   name: t("Subject"),
    //   center: false,
    //   sortable: true,
    //   // minWidth: "400px",
    //   sortField: "subject",
    //   selector: (row) => row.subject,
    //   cell: (row) => row.subject
    // },
    {
      name: t("Assigned To"),
      sortable: false,
      wrap: false,
      grow: 2,
      width: "300px",
      cell: ({ assignedTo }) => {
        if (assignedTo) {
          return assignedTo.map((assignee, index) => {
            const isLastBadge = assignedTo[assignedTo.length - 1] === index
            return (
              <Link
                key={`${assignee}-${index}`}
                to="/apps/roles"
                className={classnames({ "me-50": !isLastBadge })}
              >
                <Badge
                  pill
                  color={colorsAssignee[assignee]}
                  style={{
                    marginTop: "2px"
                  }}
                  className="text-capitalize"
                >
                  {assignee?.replace("-", " ")}
                </Badge>
              </Link>
            )
          })
        } else {
          return null
        }
      }
    },
    {
      name: t("Apps"),
      center: false,
      sortable: false,
      wrap: true,
      width: "200px",
      cell: ({ appsName }) => {
        if (appsName) {
          return appsName.map((assignee, index) => {
            const isLastBadge = appsName[appsName.length - 1] === index
            return (
              <Link
                key={`${assignee}-${index}`}
                to="/apps/app"
                className={classnames({ "me-50": !isLastBadge })}
              >
                <Badge
                  pill
                  color={colorsAssignee[assignee]}
                  className="text-capitalize"
                >
                  {assignee.replace("-", " ")}
                </Badge>
              </Link>
            )
          })
        } else {
          return null
        }
      }
    },
    {
      name: t("Resources"),
      center: false,
      sortable: false,
      width: "160px",
      selector: (row) => row.menuCount,
      cell: (row) => {
        return (
          <>
            <Link
              key="table-field-count-menu"
              to="/apps/permission_menu"
              className="me-50"
            >
              <Badge pill color="light-info" className="text-capitalize">
                {row.menuCount} {t("Menu")}
              </Badge>
            </Link>
            <Link
              key="table-field-count-endpoint"
              to="/apps/permission_endpoint"
            >
              <Badge pill color="light-warning" className="text-capitalize">
                {row.endpointCount} {t("Endpoint")}
              </Badge>
            </Link>
          </>
        )
      }
    },
    {
      name: t("Actions"),
      center: true,
      width: "140px",
      cell: (row) => (
        <GlobalActionsTable
          row={row}
          handleDelete={handleDelete}
          handleEdit={handleAddEdit}
          storeName={storeName}
        />
      )
    }
  ]

  const tableColumns = useMemo(
    () =>
      store.isLoading
        ? columns.map((column) => ({
            name: t(column.name),
            ...column,
            sortable: false,
            cell: (row) =>
              column.name === "Actions" ? (
                <>
                  <Skeleton
                    sx={{ bgcolor: colors.dark.light }}
                    animation="wave"
                    className="ms-2 me-1"
                    width={30}
                    height={40}
                  />
                  <Skeleton
                    sx={{ bgcolor: colors.dark.light }}
                    animation="wave"
                    width={30}
                    height={40}
                  />
                </>
              ) : (
                <Skeleton
                  sx={{ bgcolor: colors.dark.light }}
                  animation="wave"
                  width="100%"
                  height={40}
                />
              )
          }))
        : columns.map((column) => ({
            name: t(column.name),
            ...column
          })),
    [store.isLoading, columns]
  )

  const dataToRender = () => {
    const filters = {
      search: searchTerm
    }
    const isFiltered = Object.keys(filters).some(function (k) {
      return filters[k].length > 0
    })
    if (!store.isLoading) {
      if (store.data.length > 0) {
        return store.data
      } else if (store.data.length === 0 && isFiltered) {
        return []
      } else {
        // console.log(store.allData)
        // return store.allData.slice(0, rowsPerPage)
      }
    } else {
      return Array(10).fill({})
    }
  }

  useEffect(() => {
    if ((store.data.length === 0 || getAlwaysGrid) && !store.isLoading) {
      setLoadingText(`${t("Loading Data")} ${t(pageTitle)}`)
      dispatch(
        getDataList({
          sort_dir,
          sort,
          search: searchTerm,
          page: currentPage,
          length: rowsPerPage,
          ...filtersCustom
        })
      )
    }
    // console.log(store)
  }, [dispatch, store.data.length, sort_dir, sort, currentPage])

  return (
    <Fragment>
      <UILoader blocking={loading} loader={<Loader />}>
        <Card className="overflow-hidden">
          <div className="react-dataTable react-dataTable-selectable-rows">
            <DataTable
              noHeader
              subHeader
              sortServer
              responsive
              pagination
              paginationServer
              paginationTotalRows={store.total}
              paginationComponent={CustomPagination}
              selectableRows={deleteSelection}
              onSelectedRowsChange={handleRowSelected}
              selectableRowsHighlight
              clearSelectedRows={toggleCleared}
              selectableRowsComponent={BootstrapCheckbox}
              noDataComponent={<NoDataDisplay />}
              style={{ "min-height": "400px" }}
              highlightOnHover
              pointerOnHover
              columns={tableColumns}
              conditionalRowStyles={conditionalRowStyles}
              onSort={handleSort}
              sortIcon={<ChevronDown />}
              className="react-dataTable"
              data={dataToRender()}
              subHeaderComponent={
                <CustomHeader
                  store={store}
                  searchTerm={searchTerm}
                  rowsPerPage={rowsPerPage}
                  handleFilter={handleFilter}
                  handlePerPage={handlePerPage}
                  handleAddEdit={handleAddEdit}
                  selectedRow={selectedRows}
                  handleDeleteSelected={handleDeleteSelected}
                  handleFiltersChange={handleFiltersChange}
                  filtersCustomAdd={filtersCustomAdd}
                />
              }
            />
          </div>
        </Card>

        {/* <Sidebar open={sidebarOpen} toggleSidebar={toggleSidebar} /> */}
      </UILoader>

      {showModal && (
        <ModalAddEdit
          formState={formState}
          show={showModal}
          setShow={toggleSidebar}
          detailPage={detailPage}
          setFormState={setFormState}
        />
      )}
    </Fragment>
  )
}

export default List
